/* harmony default export */ __webpack_exports__["default"] = ("<!-- <p class=\"title3 text-center\" id=\"section-0\">STORAGE OPTIMISATION</p>\n\n<div class=\"row\">\n    <div class=\"col-md-6 title1\">\n    <img src=\"https://thumbs.dreamstime.com/b/cuadernos-conectados-con-los-servidores-8165072.jpg\" class=\"img-fluid imgmar\" alt=\"Responsive image\">\n    </div>\n  <div class=\"col-md-6 title1\">\n    <p class=\"homep\">\n            Storage optimization technologies, such as virtualization and deduplication, are excellent\nand probably can save an organization thousands of dollars. While it's undoubtedly\ninvaluable, technology often offers only part of the solution to storage optimization.\n    </p>\n  </div>\n</div>\n<p class=\"title3 text-center\" id=\"section-1\">STORAGE MANAGEMENT</p>\n<div class=\"row\">\n       \n        <div class=\"col-md-6 title\">\n            <img src=\"https://www.hnbinfo.com/wp-content/uploads/2018/02/Storage-management-1-1.jpg\" class=\"img-fluid imgmar\" alt=\"Responsive image\">\n          </div>\n          <div class=\"col-md-6 title1\">\n         \n              <p class=\"homep\">More than most other systems management processes, storage management\n                    involves a certain degree of trust. Users entrust us with the safekeeping of their data.\n                    They trust that they will be able to access their data reliably in acceptable periods of\n                    time. They trust that, when they retrieve it, it will be in the same state and condition\n                    as it was when they last stored it.</p>\n             <ul class=\"self\">\n                 <li>Capacity</li>\n                 <li>Performance</li>\n                 <li>Reliability</li>\n                 <li>Recoverability</li>\n             </ul>\n             <p class=\"homep\">Tool- PURE Storage </p>\n          </div>\n      \n        </div>\n\n         -->");

