/* harmony default export */ __webpack_exports__["default"] = ("<!-- <p class=\"title3 text-center\" id=\"section-0\">VIRTUALISATION SYSTEMS</p>\n\n<div class=\"row\">\n    <div class=\"col-md-6 title\">\n        <img src=\"https://fiverr-res.cloudinary.com/images/t_main1,q_auto,f_auto/gigs/111693827/original/f8da627548ff15bee00df1d2a766bb15561bdc00/make-a-recommendation-engine-to-your-business.png\" class=\"img-fluid imgmar\" alt=\"Responsive image\">\n      </div>\n\n  <div class=\"col-md-6 title1\">\n    <p class=\"homep\">\n            Virtualization describes a technology in which an application, guest operating system or data\nstorage is abstracted away from the true underlying hardware or software. You can look at\nall of your structure at one go if virtualized in the right way.\n    </p>\n  </div>\n</div>\n<p class=\"title3 text-center\" id=\"section-1\">OPERATIONS</p>\n<div class=\"row\">\n       \n        <div class=\"col-md-6 title\">\n            <img src=\"https://reetus.com/wp-content/uploads/2017/01/ITIL_OSA.jpg\" class=\"img-fluid imgmar\" alt=\"Responsive image\">\n          </div>\n          <div class=\"col-md-6 title1\">\n         \n              <p class=\"homep\">Virtualised System solutions for Operations will allow you to visualize, analyze\n                    and optimize performance an assess the health, risk, and efficiency of your virtual\n                    infrastructure. Virtualization of IT systems has many advantages, which is why it has\n                    become so popular. Apart from improving IT service agility, virtualization reduces the\n                    infrastructure cost of ownership by decreasing the total number of physical servers;\n                    therefore, operating expenses go down dramatically.</p>\n        \n          </div>\n      \n        </div>\n        <p class=\"title3 text-center\" id=\"section-2\">MIGRATION, BACKUP & RECOVERY</p>\n        <div class=\"row\">\n                \n                <div class=\"col-md-6 title\">\n                    <img src=\"https://hitinfrastructure.com/images/site/features/2017-07-26-phishing-attack.gif\" class=\"img-fluid imgmar\" alt=\"Responsive image\">\n                  </div>\n                  <div class=\"col-md-6 title1\">\n                 \n                      <p class=\"homep\">Transitioning must be quick and with utmost efficiency, end-users must be\n                            able to collaborate without interruption and the organization must be exposed to less\n                            risk during the migration project. We provide analysis and give solutions for backup\n                            and recovery in case of unforeseen glitches.</p>\n                \n                  </div>\n              \n                </div>\n                <p class=\"title3 text-center\" id=\"section-3\">VIRTUAL DESKTOP</p>\n                <div class=\"row\">\n                    \n                        <div class=\"col-md-6 title\">\n                            <img src=\"https://i1.wp.com/itnews.blog.fordham.edu/wp-content/uploads/2016/11/AdobeStock_67901546-2.jpg\" class=\"img-fluid imgmar\" alt=\"Responsive image\">\n                          </div>\n                          <div class=\"col-md-6 title1\">\n                         \n                              <p class=\"homep\">This approach can have many benefits, depending on the type of VDI\n                                    deployed. Because little actual computing takes place at the endpoint, IT\n                                    departments may be able to extend the lifespan of otherwise obsolete PCs by\n                                    repurposing them as VDI clients. And when the time does come to purchase new\n                                    devices, organizations can buy cheaper, less powerful machines.</p>\n                        \n                          </div>\n                      \n                        </div>\n                        <p class=\"title3 text-center\" id=\"section-4\">SECURITY FOR VIRTUAL SYSTEMS</p>\n                        <div class=\"row\">\n                               \n                                <div class=\"col-md-6 title\">\n                                    <img src=\"http://blueturtle.co.za/wp-content/uploads/2014/10/2nd-security-for-virtual-systems.jpg\" class=\"img-fluid imgmar\" alt=\"Responsive image\">\n                                  </div>\n                                  <div class=\"col-md-6 title1\">\n                                 \n                                      <p class=\"homep\">Virtualization of IT infrastructures should be coupled with the re-engineering\n                                            of security and compliance measures. The existing security policies for the physical\n                                            IT system cannot be copied blindly for the virtual IT framework. The technical team\n                                            should work with the functional team to map the existing security policies to the\n                                            virtual IT system. An internal audit program should be developed, specifically for the\n                                            virtual IT system. Proper security information management should be set up to\n                                            secure the virtualized system.</p>\n                                <p class=\"homep\">Tool – agile data recovery</p>\n                                  </div>\n                              \n                                </div> -->\n");

